___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --teal: #000;
  --teal-light: #000;
  --teal-dark: #000;
  --purple: #000;
  --purple-light: #000;
  --purple-dark: #000;
  --red: #000;
  --panel-width: 260px;
}

body {
  font-family: monospace;
  font-size: 14px;
  color: var(--purple);
  background-color: #f6f6f6;
}

body, html, #app {
  height: 100%;
  margin: 0;
}

body.dragover {
  background-color: var(--teal);
}

* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

strong {
  font-weight: 900;
}

.root {
  display: flex;
  align-items: stretch;
  height: 100%;
}

path {
  stroke: black;
  fill: transparent;
}

.preview-area {
  position: relative;
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.preview {
  box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  background-color: white;
  position: relative;
}

.preview-loader {
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
  color: whitesmoke;
  display: flex;
  font-size: 36px;
  font-weight: bold;
  height: 100%;
  justify-content: center;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.preview > svg {
  display: block;
  contain: strict;
}

.preview > svg:not(:first-child) {
  position: absolute;
  transform: translateZ(0.001px);
  top: 0;
  left: 0;
}

.dragover .drag-target {
  opacity: 0;
}

.drag-target {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
}

.drag-target-message {
  display: flex;
  justify-content: center;
  align-items: center;
  color: whitesmoke;
  font-weight: bold;
  font-size: 36px;
}

.control-panel {
  position: relative;
  flex: 0 0 var(--panel-width);
  width: var(--panel-width);
  background-color: white;
  overflow-y: scroll;

  box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.2);
}

.control-panel-bottom {
  position: fixed;
  bottom: 0;
  width: var(--panel-width);
  padding-bottom: 0.5rem;
  background-color: white;
}

.control-panel-bottom .section-header {
  padding-top: 0.25rem;
}

.saxi-title {
  margin: 0 1rem;
  padding-top: 0.5rem;
  font-size: 36px;
  font-weight: 900;
  text-align: center;
  user-select: none;
}
.disconnected .saxi-title {
  text-decoration-line: line-through;
}

.section-header {
  margin-bottom: 1rem;
  padding: 0.5rem 0;
  border-bottom: 1px solid var(--purple-light);
  font-size: 18px;
  font-weight: 900;
  text-align: center;
  user-select: none;
}

.section-body {
  padding: 0 1rem;
}

/* INPUTS */

label {
  font-size: 10px;
  text-align: center;
  line-height: 1.6;
  user-select: none;
}

input[type=number] {
  width: 100%;
  padding: 0.3rem;
  margin-bottom: 0.5rem;
  border: 1px solid var(--purple);
  font-family: 'Work Sans', sans-serif;
  font-size: 14px;
  color: var(--purple);
  background-color: white;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  outline: none;
}

input[type=checkbox] {
  position: relative;
  appearance: none;
  -webkit-appearance: none;
  width: 18px;
  height: 18px;
  margin: 0 0.5rem 0 0;
  border: 1px solid var(--purple);
  background-color: white;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  outline: none;
  cursor: pointer;
}

input[type=checkbox]:checked:after {
  display: block;
  position: absolute;
  top: 3px;
  right: 3px;
  bottom: 3px;
  left: 3px;
  content: '';
  background-color: var(--purple);
}

button, select, input[type="file"]::file-selector-button {
  width: 100%;
  margin-bottom: 0.5rem;
  padding: 0.4rem;
  font-family: 'Work Sans', sans-serif;
  font-size: 14px;
  font-weight: 700;
  background-color: white;
  color: var(--purple);
  border: 1px solid var(--purple);
  border-radius: 0;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  outline: none;
}

button:not(:disabled):hover,
button:not(:disabled):focus,
select:not(:disabled):hover,
select:focus,
input[type=number]:hover,
input[type=number]:focus,
input[type="file"]::file-selector-button:hover,
input[type="file"]::file-selector-button:focus,
input[type=checkbox]:focus {
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.25);
}

button:not(:disabled):active {
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.25), inset 2px 2px 5px rgba(0, 0, 0, 0.2);
  background-color: #fafafa;
  color: var(--purple-dark);
}

/* button-link is a button element that looks like a link */
button.button-link {
  display: block;
  padding: 0;
  border: none;
  font-size: 10px;
  text-decoration: none;
  background-color: transparent;
  box-shadow: none;
  font-weight: 400;
}

button.button-link:hover,
button.button-link:active,
button.button-link:focus {
  box-shadow: none;
  background-color: transparent;
  text-decoration: underline;
}

select {
  height: 30px;
  appearance: none;
  -webkit-appearance: none;
}

select[multiple]:focus option:checked {
  background: var(--purple);
  color: white;
}

.flex button,
.flex label {
  width: calc(50% - 0.25rem);
  align-self: flex-end;
}

.flex-checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.5rem;
}

/* PEN section */

.pen-label {
  width: calc(50% - 0.25rem);
}

/* PAPER section */

.paper-sizes {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.paper-label {
  width: 44%;
  margin-bottom: 0;
}

.paper-sizes__swap {
  width: 10%;
  margin-bottom: 16px;
  cursor: pointer;
  fill: var(--purple);
}
.paper-sizes__swap:hover {
  fill: var(--purple-dark);
}

.layer-select {
  height: 50px;
  background-color: white;
  border: 1px solid var(--purple);
  color: var(--purple);
}
.layer-select:hover {
  background-color: white;
  cursor: default;
}

.spacer {
  height: 200px;
}

/* MORE section */

summary {
  outline: none;
  cursor: pointer;
}

summary:focus {
  color: var(--purple-dark);
}

.horizontal-labels label {
  display: block;
  position: relative;
}
.horizontal-labels label::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 28px;
  border-right: 1px solid var(--purple);
}

.horizontal-labels img {
  position: absolute;
  padding: 4px;
  width: 28px;
  height: 28px;
  border-right: 1px solid var(--purple);
  /*opacity: 0.5;*/
}
.horizontal-labels input {
  padding-left: 32px;
}

/* PLOT section */

.duration {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.section-body__plot button {
  padding: 0.4rem;
  font-size: 18px;
}
.section-body__plot button:disabled {
  /*background-color: var(--purple-light);*/
  /*color: var(--purple-light);*/
  cursor: default;
}

.replan-button {
  /*color: var(--purple);*/
}

.plot-button {
  color: white;
  background-color: var(--teal);
  border: 1px solid var(--teal-dark);
}
.plot-button:not(:disabled):active {
  color: white;
  background-color: var(--teal);
}
.plot-button:disabled {
  color: var(--teal-light);
  background-color: white;
}
.plot-button--plotting {
  background-color: var(--teal-light);
}

.button-row {
  display: flex;
}
.button-row > *:not(:first-child) {
  margin-left: 10px;
}

.cancel-button {
  background-color: white;
  color: var(--purple-light);
}
.cancel-button--active {
  color: var(--purple);
}


/* UTILS */

.purple { color: var(--purple); }
.teal { color: var(--teal); }
.teal-dark { color: var(--teal-dark); }
.red { color: var(--red); }

.reg { font-weight: 400; }

.flex {
  display: flex;
  justify-content: space-between;
}

.info-disconnected {
  text-align: center;
  color: var(--red);
}

.center {
  display: flex;
  justify-content: center;

